import styled from "styled-components";
import Button from "../common/Button";

const AuthFormBlock = styled.div`
  display: flex; /* 내부 내용 중앙 정렬 */
  flex-direction: column; /* flex 컨테이너 내의 아이템을 배치 */
  justify-content: center;
  align-items: center;
`;

const StyledInput = styled.input`
  font-size: 1rem;
  border: 1px solid gainsboro;
  border-radius: 2px;
  background: #fff;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  outline: none;
  width: 100%;

  &:focus {
    border: 2px solid #B8EA8E;
  }

  & + & {
    margin-top: 0.5rem;
  }
`;

const AuthForm = ({form, onChange, onSubmit}) => {
    return (
        <AuthFormBlock>
            <form onSubmit={onSubmit}>
                <StyledInput name="email"
                             placeholder="이메일"
                             onChange={onChange}
                             value={form.email}
                />
                <StyledInput name="password"
                             placeholder="비밀번호"
                             type="password"
                             onChange={onChange}
                             value={form.password}
                />
                <Button fullWidth style={{marginTop: "1rem"}}>로그인</Button>
            </form>
        </AuthFormBlock>
    );
};

export default AuthForm;